.database-asegurados {
  margin: 3rem auto;
}

.asegurados {
  margin-top: 3rem;
  width: 100%;
  border-spacing: 0;
  border: 1px solid #000;
}
.asegurados thead {
  border: 1px solid #000;
  background-color: #3A7CA5;
}
.asegurados thead th {
  border: 1px solid #000;
  padding: 1rem;
  color: #FDFFFC;
}
.asegurados td {
  border: 1px solid #000;
  text-align: center;
}